"use client";

import { useConvexAuth } from "convex/react";
import { useAuthActions } from "@convex-dev/auth/react";
import { ReactNode } from "react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Link from "next/link";

interface AuthGuardProps {
    children: ReactNode;
    currentPage?: string;
}

export function AuthGuard({ children, currentPage }: AuthGuardProps) {
    const { isAuthenticated, isLoading } = useConvexAuth();
    const router = useRouter();

    useEffect(() => {
        if (!isLoading && !isAuthenticated) {
            router.push("/login");
        }
    }, [isAuthenticated, isLoading, router]);

    if (isLoading) {
        return (
            <div className="min-h-screen bg-[#0a0a0f] text-white flex items-center justify-center">
                <div className="flex flex-col items-center gap-4">
                    <div className="animate-spin w-8 h-8 border-2 border-indigo-500 border-t-transparent rounded-full" />
                    <p className="text-white/50">Loading...</p>
                </div>
            </div>
        );
    }

    if (!isAuthenticated) {
        return null;
    }

    return <>{children}</>;
}

// Header component with auth
interface AppHeaderProps {
    currentPage: "campaigns" | "templates" | "contacts" | "senders";
}

export function AppHeader({ currentPage }: AppHeaderProps) {
    const { signOut } = useAuthActions();

    const handleSignOut = async () => {
        await signOut();
    };

    const navItems = [
        { href: "/campaigns", label: "Campaigns" },
        { href: "/templates", label: "Templates" },
        { href: "/contacts", label: "Contacts" },
        { href: "/senders", label: "Senders" },
    ];

    return (
        <header className="border-b border-white/10 bg-black/40 sticky top-0 z-50 backdrop-blur-sm">
            <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
                <div className="flex items-center gap-6">
                    <Link href="/" className="text-2xl font-bold bg-gradient-to-r from-indigo-400 to-purple-400 bg-clip-text text-transparent">
                        Emailer
                    </Link>
                    <nav className="flex gap-4">
                        {navItems.map((item) => (
                            <Link
                                key={item.href}
                                href={item.href}
                                className={
                                    currentPage === item.label.toLowerCase()
                                        ? "text-white font-medium"
                                        : "text-white/60 hover:text-white transition-colors"
                                }
                            >
                                {item.label}
                            </Link>
                        ))}
                    </nav>
                </div>
                <button
                    onClick={handleSignOut}
                    className="px-4 py-2 text-sm text-white/60 hover:text-white hover:bg-white/10 rounded-lg transition-colors"
                >
                    Sign Out
                </button>
            </div>
        </header>
    );
}
